.play{
    font-size: 4rem;
    color: #13e6c1;
    transition: transform 1s;
    cursor: pointer;
}
.play:hover{
color: #2bf3cee3;
}
.line{
    display: none;
}
.navbar:hover .line{
    display: block;
    border-bottom: 2px #13e6bf49 solid;
width: 70%;
margin: auto;
position: absolute;
left: 50%;
transform: translateX(-50%);
bottom: 0;
-webkit-animation: mymove 1s ease; /* Safari 4.0 - 8.0 */
animation: mymove 2s ease;
}@-webkit-keyframes mymove {
    from {width: 0px;}
    to {width: 70%;}
  }
  
  /* Standard syntax */
  @keyframes mymove {
    from {width: 0px;}
    to {width: 70%;}
  }
  .navbar:hover .next{
display: block;
-webkit-animation: next-move 1s ease; /* Safari 4.0 - 8.0 */
animation: next-move 1s ease;
  }
  .next{
      display: none;
    color: #8c878782;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 10px;
    font-size: 39px;
    margin-left: 74px;
    cursor: pointer;
  }
  @keyframes next-move {
    from {margin-left: 0px;opacity: 0;}
    to {margin-left: 70px; opacity: 1;}
  }