@import url(https://use.fontawesome.com/releases/v5.8.2/css/all.css);
@import url(https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap);
body{
  font-family: 'Lexend Deca', sans-serif !important;
}
:root{
  --main-color:#13e6c1;
  --sec-color:#a49c12;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.section{
  margin-top:150px;
}
.about-me{
  font-size: 1.2rem;
}
.playlist{
  border: 1px solid #11e6c0;
  padding: 10px;
  border-radius: 3px;
}
.liner{
  margin: 4px;
}
.liner:hover{
  background: rgba(56, 55, 55, 0.322);
  cursor: pointer;
}
.liner:After{
  content: ""; /* This is necessary for the pseudo element to work. */ 
  display: block; /* This will put the pseudo element on its own line. */
  margin: 0 auto; /* This will center the border. */
  width: 100%; /* Change this to whatever width you want. */
  padding-top: 20px; /* This creates some space between the element and the border. */
  border-bottom: 1px solid rgba(255, 255, 255, 0.445); /* This creates the border. Replace black with whatever color you want. */
}

.App {
  text-align: center;
}
.eden-btn{
  background: var(--sec-color);
    height: 60px;
    padding: 5px;
    font-size: 1.2rem;
    margin: 3px;
    padding: 10px;
    border-radius: 34px;
outline: none;
-webkit-transition: all 1s;
transition: all 1s;
position: relative;
}

.eden-btn:hover{
  background:  #e2d811;
box-shadow: 0 7px 14px rgba(24, 24, 192, 0.1), 0 3px 6px rgba(16, 187, 164, 0.568);
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.profile-img{
  -webkit-transition:  1s ease-in;
  transition:  1s ease-in;
}
.profile-img:hover{
  border-radius: 60px !important;
  -webkit-transform: scale(1) perspective(1040px) rotateY(-11deg) rotateX(2deg) rotate(2deg);
          transform: scale(1) perspective(1040px) rotateY(-11deg) rotateX(2deg) rotate(2deg);

  /* transform: scale(1) perspective(1040px) rotateY(-11deg) rotateX(2deg) rotate(2deg); */
}
.box-music{
  -webkit-transition:  1s;
  transition:  1s;
}
.box-music:hover{
    -webkit-transform: scale(1) perspective(1080px) rotateY(11deg) rotateX(2deg) rotate(-4deg);
            transform: scale(1) perspective(1080px) rotateY(11deg) rotateX(2deg) rotate(-4deg);

}

.card-img{
 background-color: transparent !important;
 box-shadow: 0 !important;
}
@media (min-width: 992px){
.masthead h1 {
    font-size: 5.5rem;
}
}
.masthead h1 {
    font-size: 4rem;
    margin: 0;
    padding: 0;
}
.mb-1, .my-1 {
    margin-bottom: .25rem!important;
}
h1, h2, h3, h4, h5, h6 {
    font-weight: 700;
}
.top-text{
  vertical-align: middle;
}
.title{
  color:var(--main-color);
}
@media (min-width: 1300px)
{
  .container {
    max-width: 1260px;
}
}
@media (min-width: 1400px)
{
  .container {
    max-width: 1380px;
}
}
.fade-in{
  -webkit-animation: open 5s ease; /* Safari 4.0 - 8.0 */
  animation: open 5s ease;
}
.neon{
  box-shadow: 0px -1px 20px -1px rgba(19,230,193,1);
  border-radius: 100px;
  padding: 10px;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes open {
  from {opacity: 0;}
  to {opacity: 1;}
}

/* Standard syntax */
@keyframes open {
  from {opacity: 0;}
  to {opacity: 1;}
}
.picks{
  max-width: 500px;
  -webkit-transition:  1s ease-in;
  transition:  1s ease-in;

}
.open-btn{
  -webkit-transform: scale(1) perspective(1080px) rotateY(3deg) rotateX(70deg) rotate(-1deg);
          transform: scale(1) perspective(1080px) rotateY(3deg) rotateX(70deg) rotate(-1deg);

}
.picks:hover{
  -webkit-transform: scale(1) perspective(1080px) rotateY(3deg) rotateX(1deg) rotate(-1deg);
          transform: scale(1) perspective(1080px) rotateY(3deg) rotateX(1deg) rotate(-1deg);

}
.selected{
  background: #4e44443d !important;
}
/* .selected::after{
  font-family: "Font Awesome 5 Free"; font-weight: 900; content: "\f025";
} */
.listening-track{
  display: none;
  color: #11e6c0;
  vertical-align: middle;
}
.selected .listening-track{
  display: inline-flex !important;
  display: -o-inline-flex !important;
}


.play{
    font-size: 4rem;
    color: #13e6c1;
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    cursor: pointer;
}
.play:hover{
color: #2bf3cee3;
}
.line{
    display: none;
}
.navbar:hover .line{
    display: block;
    border-bottom: 2px #13e6bf49 solid;
width: 70%;
margin: auto;
position: absolute;
left: 50%;
-webkit-transform: translateX(-50%);
        transform: translateX(-50%);
bottom: 0;
-webkit-animation: mymove 1s ease; /* Safari 4.0 - 8.0 */
animation: mymove 2s ease;
}@-webkit-keyframes mymove {
    from {width: 0px;}
    to {width: 70%;}
  }
  
  /* Standard syntax */
  @keyframes mymove {
    from {width: 0px;}
    to {width: 70%;}
  }
  .navbar:hover .next{
display: block;
-webkit-animation: next-move 1s ease; /* Safari 4.0 - 8.0 */
animation: next-move 1s ease;
  }
  .next{
      display: none;
    color: #8c878782;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    padding: 10px;
    font-size: 39px;
    margin-left: 74px;
    cursor: pointer;
  }
  @-webkit-keyframes next-move {
    from {margin-left: 0px;opacity: 0;}
    to {margin-left: 70px; opacity: 1;}
  }
  @keyframes next-move {
    from {margin-left: 0px;opacity: 0;}
    to {margin-left: 70px; opacity: 1;}
  }
