
.App {
  text-align: center;
}
.eden-btn{
  background: var(--sec-color);
    height: 60px;
    padding: 5px;
    font-size: 1.2rem;
    margin: 3px;
    padding: 10px;
    border-radius: 34px;
outline: none;
transition: all 1s;
position: relative;
}

.eden-btn:hover{
  background:  #e2d811;
box-shadow: 0 7px 14px rgba(24, 24, 192, 0.1), 0 3px 6px rgba(16, 187, 164, 0.568);
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.profile-img{
  transition:  1s ease-in;
}
.profile-img:hover{
  border-radius: 60px !important;
  transform: scale(1) perspective(1040px) rotateY(-11deg) rotateX(2deg) rotate(2deg);

  /* transform: scale(1) perspective(1040px) rotateY(-11deg) rotateX(2deg) rotate(2deg); */
}
.box-music{
  transition:  1s;
}
.box-music:hover{
    transform: scale(1) perspective(1080px) rotateY(11deg) rotateX(2deg) rotate(-4deg);

}

.card-img{
 background-color: transparent !important;
 box-shadow: 0 !important;
}
@media (min-width: 992px){
.masthead h1 {
    font-size: 5.5rem;
}
}
.masthead h1 {
    font-size: 4rem;
    margin: 0;
    padding: 0;
}
.mb-1, .my-1 {
    margin-bottom: .25rem!important;
}
h1, h2, h3, h4, h5, h6 {
    font-weight: 700;
}
.top-text{
  vertical-align: middle;
}
.title{
  color:var(--main-color);
}
@media (min-width: 1300px)
{
  .container {
    max-width: 1260px;
}
}
@media (min-width: 1400px)
{
  .container {
    max-width: 1380px;
}
}
.fade-in{
  -webkit-animation: open 5s ease; /* Safari 4.0 - 8.0 */
  animation: open 5s ease;
}
.neon{
  -webkit-box-shadow: 0px -1px 20px -1px rgba(19,230,193,1);
  -moz-box-shadow: 0px -1px 20px -1px rgba(19,230,193,1);
  box-shadow: 0px -1px 20px -1px rgba(19,230,193,1);
  border-radius: 100px;
  padding: 10px;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes open {
  from {opacity: 0;}
  to {opacity: 1;}
}

/* Standard syntax */
@keyframes open {
  from {opacity: 0;}
  to {opacity: 1;}
}
.picks{
  max-width: 500px;
  transition:  1s ease-in;

}
.open-btn{
  transform: scale(1) perspective(1080px) rotateY(3deg) rotateX(70deg) rotate(-1deg);

}
.picks:hover{
  transform: scale(1) perspective(1080px) rotateY(3deg) rotateX(1deg) rotate(-1deg);

}
.selected{
  background: #4e44443d !important;
}
/* .selected::after{
  font-family: "Font Awesome 5 Free"; font-weight: 900; content: "\f025";
} */
.listening-track{
  display: none;
  color: #11e6c0;
  vertical-align: middle;
}
.selected .listening-track{
  display: inline-flex !important;
  display: -webkit-inline-flex !important;
  display: -o-inline-flex !important;
}

